html {
  font-family: system-ui;
  font-size: 13px;
  color: black;
}

@media (prefers-color-scheme: dark) {
  html {
    color: white;
  }
}

.App {
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(241, 241, 241);
  z-index: 1;
}

@media (prefers-color-scheme: dark) {
  .App {
    background-color: rgb(10, 10, 10);
  }
}

.iframe-wrapper iframe {
  width: 563px;
  height: 1218px;
  border: none;
  border-radius: 65px;
}

.iframe-wrapper-landscape iframe {
  width: 1218px;
  height: 563px;
  border: none;
  border-radius: 65px;
}

.info {
  position: absolute;
  top: 0;
  left: 0;
  margin-top: 6px;
  margin-left: 8px;
  z-index: 10;
}
